    "spinner.gts": "    <Spinner />\r\n    <Spinner @color=\"secondary\" />\r\n    <Spinner @isLarge={{true}} />\r",
    "stat-card.gts": "    <div class=\"row\">\r\n      <div class=\"col\">\r\n        <StatCard @label=\"Key 1\" @value=\"0\" />\r\n      </div>\r\n      <div class=\"col\">\r\n        <StatCard @label=\"Key 2\" @value=\"12\" />\r\n      </div>\r\n      <div class=\"col\">\r\n        <StatCard @label=\"Key 3\" @value=\"123\" />\r\n      </div>\r\n      <div class=\"col\">\r\n        <StatCard\r\n          @label=\"Key 4\"\r\n          @value=\"1,234\"\r\n          @icon={{faUser}}\r\n          @route=\"index\"\r\n        />\r\n      </div>\r\n      <div class=\"col\">\r\n        <StatCard @label=\"Key 5\" @icon={{faUser}} @route=\"index\">\r\n          Block\r\n        </StatCard>\r\n      </div>\r\n    </div>\r",
