    "table.gts": "    <Table\r\n      @data={{@controller.model}}\r\n      @nextButtonLabel=\"Next\"\r\n      @previousButtonLabel=\"Previous\"\r\n      @viewingLabel=\"Viewing\"\r\n      @ofLabel=\"of\"\r\n      @resultsLabel=\"results\"\r\n      as |table|\r\n    >\r\n      <table.header as |header|>\r\n        <header.column @prop=\"email\">\r\n          Email\r\n        </header.column>\r\n        <header.column @prop=\"firstName\">\r\n          First Name\r\n        </header.column>\r\n        <header.column @prop=\"lastName\">\r\n          Last Name\r\n        </header.column>\r\n      </table.header>\r\n      <table.body as |body user|>\r\n        <body.row as |row|>\r\n          <row.cell>\r\n            {{user.email}}\r\n          </row.cell>\r\n          <row.cell>\r\n            {{user.firstName}}\r\n          </row.cell>\r\n          <row.cell>\r\n            {{user.lastName}}\r\n          </row.cell>\r\n        </body.row>\r\n      </table.body>\r\n    </Table>\r",
    "table1.gts": "    <Table\r\n      @data={{@controller.model}}\r\n      @isSortable={{true}}\r\n      @nextButtonLabel=\"Next\"\r\n      @previousButtonLabel=\"Previous\"\r\n      @viewingLabel=\"Viewing\"\r\n      @ofLabel=\"of\"\r\n      @resultsLabel=\"results\"\r\n      as |table|\r\n    >\r\n      <table.header as |header|>\r\n        <header.column @prop=\"email\" @sort=\"asc\">\r\n          Email\r\n        </header.column>\r\n        <header.column @prop=\"firstName\">\r\n          First Name\r\n        </header.column>\r\n        <header.column @prop=\"lastName\">\r\n          Last Name\r\n        </header.column>\r\n      </table.header>\r\n      <table.body />\r\n    </Table>\r",
    "table2.gts": "    <Table\r\n      @data={{@controller.model}}\r\n      @isSortable={{true}}\r\n      @pagination=\"local\"\r\n      @nextButtonLabel=\"Next\"\r\n      @previousButtonLabel=\"Previous\"\r\n      @viewingLabel=\"Viewing\"\r\n      @ofLabel=\"of\"\r\n      @resultsLabel=\"results\"\r\n      as |table|\r\n    >\r\n      <table.header as |header|>\r\n        <header.column @prop=\"email\" @sort=\"asc\">\r\n          Email\r\n        </header.column>\r\n        <header.column @prop=\"firstName\">\r\n          First Name\r\n        </header.column>\r\n        <header.column @prop=\"lastName\">\r\n          Last Name\r\n        </header.column>\r\n      </table.header>\r\n      <table.body />\r\n    </Table>\r",
    "table3.gts": "    <Table\r\n      @data={{@controller.model}}\r\n      @isSortable={{true}}\r\n      @pagination=\"cursor\"\r\n      @nextButtonLabel=\"Next\"\r\n      @previousButtonLabel=\"Previous\"\r\n      @viewingLabel=\"Viewing\"\r\n      @ofLabel=\"of\"\r\n      @resultsLabel=\"results\"\r\n      @canNext={{false}}\r\n      @canPrevious={{false}}\r\n      @onNext={{@controller.next}}\r\n      @onPrevious={{@controller.previous}}\r\n      as |table|\r\n    >\r\n      <table.header as |header|>\r\n        <header.column @prop=\"email\" @sort=\"asc\">\r\n          Email\r\n        </header.column>\r\n        <header.column @prop=\"firstName\">\r\n          First Name\r\n        </header.column>\r\n        <header.column @prop=\"lastName\">\r\n          Last Name\r\n        </header.column>\r\n      </table.header>\r\n      <table.body />\r\n    </Table>\r",
    "table4.gts": "    <Table\r\n      @data={{@controller._page}}\r\n      @isSortable={{true}}\r\n      @pagination=\"offset\"\r\n      @nextButtonLabel=\"Next\"\r\n      @previousButtonLabel=\"Previous\"\r\n      @viewingLabel=\"Viewing\"\r\n      @ofLabel=\"of\"\r\n      @resultsLabel=\"results\"\r\n      @page={{@controller.page}}\r\n      @totalItems={{@controller.model.length}}\r\n      @onChangePage={{fn (mut @controller.page)}}\r\n      as |table|\r\n    >\r\n      <table.header as |header|>\r\n        <header.column @prop=\"email\" @sort=\"asc\">\r\n          Email\r\n        </header.column>\r\n        <header.column @prop=\"firstName\">\r\n          First Name\r\n        </header.column>\r\n        <header.column @prop=\"lastName\">\r\n          Last Name\r\n        </header.column>\r\n      </table.header>\r\n      <table.body />\r\n    </Table>\r",
    "theme.gts": "      {{theme @controller.theme}}\r\n\r\n      <FormCheck\r\n        @value={{@controller.isDarkTheme}}\r\n        @label=\"Dark theme\"\r\n        @identifier=\"isDarkTheme\"\r\n        @requiredLabel=\"Required\"\r\n        @isSwitch={{true}}\r\n        @onChange={{fn (mut @controller.isDarkTheme)}}\r\n      />\r",
    "timestamp.gts": "      <ul>\r\n        <li>{{timestamp @controller.date}}</li>\r\n        <li>{{timestamp @controller.date utc=true}}</li>\r\n        <li>{{timestamp @controller.date format=\"date\"}}</li>\r\n        <li>{{timestamp @controller.date format=\"time\"}}</li>\r\n        <li>{{timestamp @controller.date format=\"numberDate\"}}</li>\r\n        <li>{{timestamp @controller.date format=\"year\"}}</li>\r\n        <li>{{timestamp @controller.date format=\"full\"}}</li>\r\n      </ul>\r",
