    "toast2.gts": "    <Toast @closeButtonLabel=\"Close\" @onClose={{@controller.close}}>\r\n      <:title>Title</:title>\r\n      <:body>Body</:body>\r\n    </Toast>\r",
    "toast3.gts": "    <Toast\r\n      @closeButtonLabel=\"Close\"\r\n      @color=\"primary\"\r\n      @onClose={{@controller.close}}\r\n    >\r\n      <:title>Title</:title>\r\n      <:body>Body</:body>\r\n    </Toast>\r",
    "toast4.gts": "    <Toast\r\n      @closeButtonLabel=\"Close\"\r\n      @color=\"success\"\r\n      @onClose={{@controller.close}}\r\n    >\r\n      <:title>Title</:title>\r\n      <:body>Body</:body>\r\n    </Toast>\r",
