  /**
   * Determines file type from name.
   * @param name
   * @returns
   */
  function fileType(name) {
    const types = {
      image: ['png', 'jpg', 'jpeg'],
      audio: ['mp3'],
      pdf: ['pdf'],
      csv: ['csv'],
      zip: ['zip'],
      word: ['doc', 'docx'],
      excel: ['xls', 'xlsx'],
      outlook: ['msg']
    };
    const parts = name.split('.');
    const extension = parts[parts.length - 1];
    if (extension) {
      let type;
      for (type in types) {
        const extensions = types[type];
        if (extensions.includes(extension)) {
          return type;
        }
      }
    }
    return null;
  }
